* {
  font-family: Roboto, sans-serif;
}

body {
  height: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.flex {
  display: flex;
}

.header {
  height: 216px;
  background-image: url("header-background-mobile@1x.3dabdd54.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  display: flex;
}

.header__logo {
  align-items: flex-start;
  gap: 12px;
  display: flex;
  position: absolute;
  top: 40px;
  left: 20px;
}

.header__logo--film {
  width: 24px;
  height: 22.85px;
  fill: rgba(0, 0, 0, 0);
  margin-top: 5px;
}

.header__logo--span {
  text-align: center;
  color: #fff;
  align-items: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 35px;
  display: flex;
}

.header__buttons {
  text-transform: uppercase;
  color: #fff;
  gap: 40px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  display: flex;
  position: absolute;
  top: 50px;
  right: 20px;
}

.header__search {
  width: 394px;
  height: 20px;
  border-bottom: .5px solid #fff;
  align-items: center;
  margin-top: 55px;
  display: flex;
}

.header__search--input {
  width: 100%;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 0;
}

.header__search--search {
  width: 12px;
  height: 12px;
  fill: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .header {
    background-image: url("header-background-tablet@1x.5ccb66f4.jpg");
  }
}

@media screen and (min-width: 768px), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .header {
    background-image: url("header-background-tablet@2x.fa7e29c7.jpg");
  }
}

@media screen and (min-width: 1280px) {
  .header {
    background-image: url("header-background@1x.f9760ef2.jpg");
  }
}

@media screen and (min-width: 1280px), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .header {
    background-image: url("header-background@2x.0842fa6d.jpg");
  }
}

@media screen and (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .header {
    background-image: url("header-background-mobile@2x.218945a1.jpg");
  }
}

a:link {
  color: #fff;
  text-decoration: none;
}

a:visited {
  color: #fff;
}

#pagination {
  flex-shrink: 0;
  margin-top: 0;
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  #pagination {
    margin-bottom: 60px;
  }
}

#pagination .tui-page-btn {
  color: #000;
  border: none;
  font-weight: 500;
  line-height: 1.2px;
}

@media (min-width: 768px) {
  #pagination .tui-page-btn {
    font-size: 14px;
  }
}

#pagination .tui-is-selected {
  width: 25px;
  height: 25px;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  background-color: #ff6b08;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  display: inline-flex;
}

#pagination .tui-is-selected:hover {
  background-color: #ff6b08;
}

@media (min-width: 768px) {
  #pagination .tui-is-selected {
    width: 40px;
    height: 40px;
  }
}

#pagination .tui-ico-first, #pagination .tui-ico-last {
  margin-top: 8px;
}

@media (min-width: 768px) {
  #pagination .tui-ico-first {
    margin-right: 10px;
  }
}

@media (min-width: 768px) {
  #pagination .tui-ico-last {
    margin-left: 10px;
  }
}

#pagination .tui-prev {
  margin-right: 5px;
}

@media (min-width: 768px) {
  #pagination .tui-prev {
    margin-right: 10px;
  }
}

#pagination .tui-next {
  margin-left: 5px;
}

@media (min-width: 768px) {
  #pagination .tui-next {
    margin-left: 10px;
  }
}

#pagination .tui-prev, #pagination .tui-next {
  width: 25px;
  height: 25px;
  background-color: #f7f7f7;
  border-radius: 5px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

#pagination .tui-prev:hover, #pagination .tui-next:hover {
  color: #fff;
  background-color: #d4d4d4;
}

@media (min-width: 768px) {
  #pagination .tui-prev, #pagination .tui-next {
    width: 40px;
    height: 40px;
  }
}

#pagination .tui-ico-next, #pagination .tui-ico-prev {
  width: 16px;
  height: 16px;
  background-position: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#pagination .tui-ico-prev {
  background-image: url("arrow-left.9cf9051e.svg");
}

#pagination .tui-ico-next {
  background-image: url("arrow-right.91990ac8.svg");
}

.footer {
  height: 100px;
  width: 100%;
  background: #f7f7f7;
  justify-content: center;
  align-items: center;
  padding: 0 73px;
  display: flex;
  position: absolute;
}

.footer__span {
  text-align: center;
  color: #545454;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  position: relative;
}

.footer__svg {
  margin: 11.8px 10px 0;
}

.overflow-off {
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
}

.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .25);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

.modal {
  height: calc(100% - 20px);
  width: 280px;
  background: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 48px 20px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .modal {
    width: 704px;
    height: 552px;
    flex-direction: row;
    gap: 68px;
    padding: 40px 72px 40px 36px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 806px;
    height: 558px;
    flex-direction: row;
    gap: 16px;
    padding: 40px 12px;
  }
}

.modal__title {
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
}

.modal__img {
  width: 240px;
  height: 357px;
  border-radius: 5px;
  margin-bottom: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .modal__img {
    width: 264px;
    height: 373.55px;
  }
}

@media screen and (min-width: 1280px) {
  .modal__img {
    width: 375px;
    height: 478px;
  }
}

